import React, { useEffect, useState } from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { Parallax, Pagination, Navigation, Autoplay } from "swiper";

// import arrowRightWhite from '../assets/right-arrow-white-icon.png';


const MainBanner = () => { 
  const [width, setWidth] = useState(window.innerWidth);

  const widthResize = () => {
    setWidth(window.innerWidth);
  }

  useEffect(() => {
    window.addEventListener("resize", widthResize);
    return () => {
      window.removeEventListener("resize", widthResize);
    }
  }, []);

 
  

  return (
    <div className="main-banner">
      <h3 className="ir-so"> </h3>
      <Swiper
        modules={[Parallax, Navigation, Pagination, Autoplay]}
        speed={600}
        parallax={true}
        slidesPerView={1}
        // navigation={true}
        pagination={{
          type: "progressbar",
        }}
        autoplay={{ delay: 3000 }}
      >
        <SwiperSlide style={{
          height: width > 1200 ? width * 0.322916 : ( width <= 768 ? width * 0.6111111 : width * 0.4296875 )
        }}>
          <div className="container">
            <div className="banner-txt">
              <p data-swiper-parallax="-200">SINCE 1867</p>
              <p data-swiper-parallax="-400">  <span><img src="/assets/right-arrow-white-icon.png" alt=""/></span> </p>
              <p data-swiper-parallax="-700">   No.1 </p>
            </div>
          </div>
        </SwiperSlide>
        <SwiperSlide style={{
          height: width > 1200 ? width * 0.322916 : ( width <= 768 ? width * 0.6111111 : width * 0.4296875 )
        }}>
          <div className="container">
            <div className="banner-txt">
              <p data-swiper-parallax="-200"> </p>
              <p data-swiper-parallax="-400">    <span><img src="/assets/right-arrow-white-icon.png" alt=""/></span> </p>
              <p data-swiper-parallax="-700">    </p>
            </div>
          </div>
        </SwiperSlide>
        <SwiperSlide style={{
          height: width > 1200 ? width * 0.322916 : ( width <= 768 ? width * 0.6111111 : width * 0.4296875 )
        }}>
          <div className="container">
            <div className="banner-txt">
              <p data-swiper-parallax="-200">  </p>
              <p data-swiper-parallax="-400">  <span><img src="/assets/right-arrow-white-icon.png" alt=""/></span></p>
              <p data-swiper-parallax="-700">  ,    </p>
            </div>
          </div>
        </SwiperSlide>
      </Swiper>
    </div>
  )
}
export default MainBanner;