import React, { useRef, useState } from "react";
import InquiryList from "./InquiryList";

const InquiryForm = () => { 
  const typeSelect = useRef();
  const authorInput = useRef();
  const numberInput = useRef();
  const emailInput = useRef();
  const emailSiteSelect = useRef();
  const contentTextarea = useRef();
  const checkedInput = useRef();
  
  const [state, setState] = useState({
    type: "normal",
    author: "",
    number: "",
    email: "",
    emailSite: "defalut",
    content: "",
  });

  const [checked, setChecked] = useState(false);

  const [data, setData] = useState([]);

  const dataId = useRef(0)

  const changeCheckedFnc = () => { 
    setChecked(!checked);
  }

  const changeStateFnc = (e) => { 
    setState({
      ...state,
      [e.target.name]: e.target.value
    });
  }

  const onCreateFnc = (type, author, content) => { 
    const newItem = {
      type,
      author,
      content,
      id: dataId.current
    };
    dataId.current += 1;
    setData([newItem, ...data]);
  }

  const submitFnc = () => { 
    if (state.author.length < 1) { 
      authorInput.current.focus();
      authorInput.current.style.borderColor = '#E00000';
      return;
    } 
    if (state.number.length < 5) { 
      numberInput.current.focus();
      numberInput.current.style.borderColor = '#E00000';
      return;
    } 
    if (state.email.length < 1) { 
      emailInput.current.focus();
      emailInput.current.style.borderColor = '#E00000';
      return;
    } 
    if (state.content.length < 5) {
      contentTextarea.current.focus();
      contentTextarea.current.style.borderColor = '#E00000';
      return;
    }
    if (!checked) {
      alert("    .")
      checkedInput.current.focus();
      return;
    }
    
    onCreateFnc(state.type, state.author, state.content);
    alert(" !");

    authorInput.current.blur();
    numberInput.current.blur();
    emailInput.current.blur();
    contentTextarea.current.blur();
    checkedInput.current.blur();
    authorInput.current.style.borderColor = 'inherit';
    numberInput.current.style.borderColor = 'inherit';
    emailInput.current.style.borderColor = 'inherit';
    contentTextarea.current.style.borderColor = 'inherit';

    setState({
      type: "",
      author: "",
      number: "",
      email: "",
      emailSite: "naver",
      content: "",
    });
    
  }

  return (
    <div className="inquiry">
      <h3 className="title"></h3>
      <div className="subTitle">   ,       . <br />
            .</div>
      <div className="form-wrap">
        <form>
          <fieldset>
            <div className="form-list">
              <label htmlFor="inquiryType"> 
              <select
                id="inquiryType"
                name="type"
                value={state.type}
                onChange={changeStateFnc}
                ref={typeSelect}>
                  <option value={""}> </option>
                  <option value={""}> </option>
                  <option value={""}> </option>
                </select>
              </label>
            </div>
            <div className="form-list">
              <label htmlFor="inquiryName">
                <input
                  type="text"
                  id="inquiryName"
                  name="author"
                  value={state.author}
                  className="input"
                  placeholder=" "
                  onChange={changeStateFnc}
                  ref={authorInput}
                />
              </label>
            </div>
            <div className="form-list">
              <label htmlFor="inquiryNumber">
                <input
                  type="number"
                  id="inquiryNumber"
                  name="number"
                  value={state.number}
                  className="input"
                  placeholder=" "
                  onChange={changeStateFnc}
                  ref={numberInput}
                />
              </label>
              <span className="vldtn">  .</span>
            </div>
            <div className="form-list form-email">
              <label htmlFor="inquiryEmail">
                <div>
                  <input
                    type="email"
                    id="inquiryEmail"
                    name="email"
                    value={state.email}
                    className="input"
                    placeholder=" "
                    onChange={changeStateFnc}
                    ref={emailInput}
                  />
                  <select
                    name="emailSite"
                    value={state.emailSite}
                    onChange={changeStateFnc}
                    ref={emailSiteSelect}>
                      <option value={"naver"}>@naver.com</option>
                      <option value={"nate"}>@nate.com</option>
                      <option value={"gmail"}>@gmail.com</option>
                      <option value={"daum"}>@daum.net</option>
                      <option value={"hanmail"}>@hanmail.com</option>
                  </select>
                </div>
              </label>
            </div>
            <div className="form-list form-text">
              <label htmlFor="inquiryCont">
                <textarea
                  rows="10"
                  id="inquiryCont"
                  name="content"
                  value={state.content}
                  className="input"
                  placeholder="  "
                  onChange={changeStateFnc}
                  ref={contentTextarea}
                />
              </label>
            </div>
            <div className="form-check-list">
              <label htmlFor="formCheck">
                <input
                  type="checkbox"
                  id="formCheck"
                  name="validation"
                  value={checked}
                  onChange={changeCheckedFnc}
                  ref={ checkedInput }
                />    .
              </label>
            </div>
          </fieldset>
        </form>
        <button className="btn-blue" onClick={submitFnc}> </button>
      </div>
      <InquiryList inquiryList={ data } />
    </div>
  )




}

export default InquiryForm;