import React, { useState, useEffect, useRef } from "react";
import { NavLink } from 'react-router-dom';
import MainBanner from '../components/MainBanner';
import MainPrdListSlide from '../components/MainPrdListSlide';
import MainCampaign from '../components/MainCampaign';

import "swiper/css";
import "swiper/css/scrollbar";
import "swiper/css/navigation";
import "swiper/css/pagination";


const Main = () => {
  const [width, setWidth] = useState(window.innerWidth);
  const [jwHouseWidth, setJwHouseWidth] = useState(0);

  const widthResize = () => {
    setWidth(window.innerWidth);
    jwHouseResize();
  }
  
  const jwHouseList = useRef();

  const jwHouseResize = () => { 
    setJwHouseWidth(jwHouseList.current.clientWidth);
  }

  useEffect(() => {
    jwHouseResize();
    
    window.addEventListener("resize", widthResize);
    return () => {
      window.removeEventListener("resize", widthResize);
    }
  });

  return (
    <div className="main-page">
      <h2 className="ir-so">  </h2>
      <MainBanner />
      <MainPrdListSlide />
      <div className="page-wrap main-sns-imgs">
        <h3 className="ir-so"> SNS </h3>
        <div className="container">
          <h4 className="title">      </h4>
          <p className="subTitle">    .        2        ,   .
          </p>
          <div className="sns-imgs">
            <a href="/" className="img1"><img src="/assets/sns-1.jpg" alt=" SNS 1"/></a>
            <a href="/" className="img5"><img src="/assets/sns-51.jpg" alt=" SNS 5"/></a>
            <a href="/" className="img2"><img src="/assets/sns-2.jpg" alt=" SNS 2"/></a>
            <a href="/" className="img3"><img src="/assets/sns-3.jpg" alt=" SNS 3"/></a>
            <a href="/" className="img4"><img src="/assets/sns-4.jpg" alt=" SNS 4"/></a>
          </div>
        </div>
      </div>
      <div className="limited-edition">
        <h3 className="ir-so"> </h3>
        <div className="container">
          <div className="limited-edition-row">
            <img src="/assets/jw-limitedEdition.png" alt="    "/>
            <div className="limited-edition-desc">
              <h4 className="title">    </h4>
              <p>       ''        . <br />

        .<br />
                
   (big bold flavours)       . </p>
              <NavLink to={"/PrdList/PrdDetail"}>  <img src="/assets/right-arrow-blue-icon.png" alt=" " /></NavLink>
            </div>
          </div>
        </div>
      </div>
      <div className="page-wrap jw-house">
        <h3 className="ir-so">  </h3>
        <div className="container">
          <h4 className="title">     </h4>
          <p className="subTitle">            .</p>
          <div className="jw-house-row">
              <div className="jw-house-list" ref={ jwHouseList }>
                <a href="/JwStory">
                  <div>
                    <div className="jw-house-bg bg1" style={{
                      height: jwHouseWidth * 0.5559322
                    }}>
                  </div>
                  <div className="jw-house-desc-lg">
                      <div>
                        <em></em>
                        <p>       .
         .       HD  . 
      ,      ,   ,      .
        "" 4    ,  ,  ,          .      , 100   .        .</p>
                      </div>
                  </div>
                </div>
                <div className="jw-house-desc-sm">
                  <p>       .        .       HD  .     ,      ,   ,      .       "" 4    ,  ,  ,          .      , 100   .        .</p>
                </div>
              </a>
            </div>
            <div className="jw-house-list" ref={ jwHouseList }>
              <a href="/JwStory">
                <div>
                  <div className="jw-house-bg bg2" style={{
                      height: jwHouseWidth * 0.5559322
                    }}> 
                  </div>
                  <div className="jw-house-desc-lg">
                    <div>
                        <em></em>
                    <p>      .  ,     ,              .      '    '   .        ,      .</p>
                    </div>
                  </div>
                </div>
                <div className="jw-house-desc-sm">
                  <p>      .  ,     ,              .      '    '   .        ,      .</p>
                </div>
              </a>
            </div>
          </div>
          <NavLink to={"/JwStory/JwHouse"} className="btn-blue">  </NavLink>
        </div>
      </div>
      <div className="line-banner" style={{
          height: width > 1200 ? width * 0.1770833 : ( width < 768 ? width * 0.3555555 : width * 0.192708333)
        }}>
      </div>
      <MainCampaign />
    </div>
  )
}

export default Main;
