import React, { useState, useEffect } from "react";

const MainCampaign = () => {
  const [counter, setCounter] = useState(
    () => JSON.parse(window.localStorage.getItem("counter")) || 145491
  );

  const numCounter = () => { 
    alert(" ");
    return setCounter((counter + 1));
  }

  useEffect(() => {
    window.localStorage.setItem("count", JSON.stringify(counter));
  }, [counter]);

  return (
    <div className="page-wrap jw-campaign">
        <h3 className="ir-so"> </h3>
        <div className="container">
          <h4 className="title">     </h4>
          <p className="subTitle">
     . <br />
        .
            <br />
            <br />
                  
          </p>
          <div className="video-hashtag">
            <span>#IMNOTDRIVING</span>
            <span>#NODRUNKDRIVING</span>
          </div>
          <div className="video-wrap">
            <iframe width="100%" height="100%" src="https://www.youtube.com/embed/56b09ZyLaWk" title="YouTube video player" ></iframe>
          </div>
          <p className="campaign-desc">
                 .<br/>
      
          </p>
        <p className="campaign-prtcp"><em>{counter.toLocaleString()}</em></p>
          <button className="btn-blue" onClick={numCounter}>JOIN THE PACT</button>
      </div>
    </div>
  )
}

export default MainCampaign;